.mincart{
  position: fixed;
  width: 50px;
  height: 50px;
  background-color: #fff;
  top: 72%;
  left:80%;
  border-radius: 25px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
