}
.mincart div{
  background-color: #F77062;
  width: 25px;
  height: 25px;
  text-align: center;
  line-height: 20px;
  border-radius: 12.5px;
  color: #fff;
  position: absolute;
  top:-7px;
  font-weight: 600;
  left:32px;
}



